.App{
  background: linear-gradient(45deg, #2B3752, #2E6E65, #86EE60);
  height: 100vh;
}
.timer-wrapper {
  margin-top: 20vh;
  width: 60vw;
  background-color: #001C30;
  text-align: center;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 8rem auto 0 auto;
  color: #fff;
  border-radius: 10px;
  box-shadow: 10px 12px 10px #001c30ce;
}

.title {
  text-align: center;
}
h2{
  text-align: center;
  color: #fff;
}

.stopwatch {
  font-size: 5rem;
  margin-right: 1rem;
}

label {
  margin-bottom: 0.5rem;
}

input {
  width: 10vw;
  margin-right: 1rem;
  font-size: 4rem;
  text-align: center;
  font-weight: 600;
  padding: 0px 5px;
}

.btn{
  padding: 20px 25px 11px 25px;
    border: none;
    border-radius: 22px;
    font-size: 2rem;
    margin: 20px 10px;
    color: #fff;
}
.btn-play{
  background-color: #3379E4;
}
.btn-stop{
  background-color: #C40018;
}
.btn-pause{
  background-color: #F2A51A;
}
.countdown-timer{
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media(max-width: 650px){
  input{
font-size: 2rem;
  }
}